@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
header{width:100%; height:50px; _background:cadetblue; color:#fff;border-bottom: 1px solid #4CBB17;}
header .center{display:flex; justify-content: space-between; padding:10px 0; align-items: center;}
header .logo{color:#4CBB17}
header .menu{width:30px; height:30px; border:1px solid #4CBB17; display:flex; align-items: center; justify-content: center;}
header .menu i.fa{font-size:22px; color:#4CBB17}
header a{color:#4CBB17}

.recipes  h2{
  background: #4CBB17;
  display: block;
  width: 100%;
  /* height: 45px; */
  padding: 10px 0;
  text-align: center;
color: #000;
}

.recipes img{
  display: block;
  width: 100%;
  /* height: 100%; */
  object-fit: cover;
}
.recipes ul{
  padding: 0 0 20px;
  font-size: 18px;
}
.recipes ul li{
  padding: 5px 30px;
  color: #ddd;
  border-bottom: 1px solid #efefef;
}
.recipes ul li:nth-last-child(1){
  border:none

}

.recipes p{
  text-align: end;
  padding: 5px 15px 5px;
color: #777;
}
.recipes a{
  color: #000;
}

*{margin:0; padding:0; box-sizing: border-box;}
ol, ul{list-style:none;}
body{font-family: 'Roboto', sans-serif; font-size:14px;background: #000;color: #efefef;}
.center{width:90%; height:auto; margin:auto; }
a{text-decoration: none;}


.App{display:flex; width:100%; height:auto; flex-direction: column;}
.App .search-form .center{width:100%; height:auto; display:flex; justify-content: center; padding:10px 0; border-bottom:1px solid #4CBB17;}
.App .search-form input{padding:5px 20px; border:1px solid #4CBB17; width:80%;background: #000;height: 40px;color: #fff;}
.App .search-form input:focus{border:1px solid #4CBB17;}
.App .search-form button{width:10%; background:#000; color:#4CBB17; border:0; margin-left:5px; border:1px solid #4CBB17}

.loader{
  width: 100%;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  align-items: center;
  justify-content: center;

}
.loader img{width: 40px;}
